import menu from './menu.hbs';
import posts from './menu.json';
import './styles.css';

const switchInput = document.querySelector('.js-switch-input');
const theme = localStorage.getItem('Theme');
const currentTheme = theme ? theme : null;
const body = document.querySelector('body');
if (currentTheme === 'LIGHT') {
  body.classList.add('light-theme');
  body.classList.remove('dark-theme');
  switchInput.checked = false;
} else if (currentTheme === 'DARK') {
  body.classList.remove('light-theme');
  body.classList.add('dark-theme');
  switchInput.checked = true;
}

const refs = {
  jsMenu: document.querySelector('.js-menu'),
};

menuItemPost(posts);

function menuItemPost(posts) {
  const markup = posts.map(post => menu(post)).join('');

  refs.jsMenu.insertAdjacentHTML('beforeend', markup);
}

const Theme = {
  LIGHT: 'light-theme',
  DARK: 'dark-theme',
};

switchInput.addEventListener('change', switchTheme, false);

function switchTheme(value) {
  if (value.target.checked) {
    document.querySelector('body').classList.remove('light-theme');
    document.querySelector('body').classList.add('dark-theme');
    localStorage.setItem('Theme', 'DARK');
  } else {
    document.querySelector('body').classList.add('light-theme');
    document.querySelector('body').classList.remove('dark-theme');
    localStorage.setItem('Theme', 'LIGHT');
  }
}
